import React, { Component } from 'react';
import '../style/App.css';
import Blog from './blog/blog.js'
import Project from './project/project.js'
import Sidebar from './sidebar/sidebar.js'

class App extends Component {
  render() {
    return (
      <div>
        <Head />
        <Body />
      </div>
    );
  }
}

const Head = () => (
  <div>
    <title>TrakBit - Harsh Vardhan - React Consultant</title>
    <meta charSet="utf-8" />
    <meta name="viewport" content="width=device-width, initial-scale=1" />
    <meta name="trakbit" content="React Developer" />
    <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css" />
  </div>
);

const Navigation = () => (
  <div className="d-flex flex-column flex-md-row align-items-center p-3 px-md-4 mb-3 bg-dark border-bottom box-shadow">
    <h3 className="my-0 mr-md-auto text-light font-weight-normal">TrakBit</h3>
  </div>
)

const Welcome = () => (
  <div className="pricing-header px-3 py-3 pt-md-5 pb-md-4 mx-auto text-center">
    <h5><p>Thriving to build the high quality and performant mobile and web applications by<br />
      making the most from the tools in JavaScript ecosystem.</p></h5>
  </div>
)

const Container = () => (
  <div className="text-center">
    <div className="container">
      <div className="row">
        <Blog />
        <Project />
        <Sidebar />
      </div>
    </div>
  </div>
)

const Body = () => (
  <div id="top">
    <Navigation />
    <Welcome />
    <Container />
  </div>
);

export default App;



